import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';
import { EVENT_TYPE } from '../../../../constants';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  navListItem: {
    backgroundClip: 'padding-box',
    padding: 0,
    [theme.breakpoints.down('md')]: {
      borderBottom: `1px solid ${theme.mixins.Black(120)}`,
    },
  },
  navigationList: {
    listStyle: 'none',
    padding: '0',
    [theme.breakpoints.up('lg')]: {
      display: 'flex',
      ...theme.mixins.paddingLeft(pxToRem(20)),
      ...theme.mixins.paddingRight(pxToRem(20)),
    },
    '& .MuiListItem-dense': {
      padding: 0,
    },
    '& .MuiListItem-root': {
      [theme.breakpoints.up('lg')]: {
        paddingTop: theme.spacing(0),
        paddingBottom: theme.spacing(0),
        width: 'auto',
        ...theme.mixins.marginRight('5%'),
        minHeight: pxToRem(75),
        display: 'flex',
        alignItems: 'center',
        '&:last-child': {
          ...theme.mixins.marginRight(theme.spacing(0)),
        },
      },
    },
    '& .nav-text': {
      paddingTop: theme.spacing(3),
      paddingBottom: theme.spacing(3),
      width: '100%',
      color: 'inherit',
      display: 'block',
      textAlign: theme.mixins.textLeft(),
      [theme.breakpoints.up('lg')]: {
        paddingTop: '0',
        paddingBottom: '0',
        width: 'auto',
      },
      '&:hover::after': {
        [theme.breakpoints.up('lg')]: {
          transform: 'scaleX(1)',
        },
      },
      '&::after': {
        [theme.breakpoints.up('lg')]: {
          display: 'block',
          content: '""',
          borderBottom: `4px solid ${theme.mixins.AbuDhabiRed()}`,
          position: 'absolute',
          top: pxToRem(98),
          width: '100%',
          transform: 'scaleX(0)',
          transition: 'transform 0.3s ease-in-out',
        },
      },
      '& .MuiSvgIcon-root': {
        fontSize: theme.spacing(2),
        position: 'absolute',
        ...theme.mixins.right(0),
        top: `calc(50% - ${pxToRem(8)})`,
        pointerEvents: 'none',
        transform: theme.mixins.rotate0(),
      },
      '&.selected': {
        color: theme.mixins.AbuDhabiRed(),
        '&:hover::after': {
          [theme.breakpoints.up('lg')]: {
            transform: 'scaleX(1)',
          },
        },
        '&::after': {
          [theme.breakpoints.up('lg')]: {
            top: pxToRem(98),
            transform: 'scaleX(1)',
            borderBottom: `4px solid ${theme.mixins.AbuDhabiRed()}`,
          },
        },
      },
      '&:hover': {
        [theme.breakpoints.up('lg')]: {
          color: theme.mixins.AbuDhabiRed(),
        },
      },
      '&.active': {
        color: ({ contentType }) =>
          contentType === EVENT_TYPE
            ? theme.mixins.ADCalendar()
            : `${theme.mixins.AbuDhabiRed()}`,
        '&::after': {
          [theme.breakpoints.up('lg')]: {
            borderBottom: '4px solid',
          },
        },
      },
    },
    '& .MuiListItem-divider': {
      [theme.breakpoints.up('lg')]: {
        borderBottom: 'none',
      },
    },
  },
  flyout: {
    height: `calc(100% - ${pxToRem(81)})`,
    left: '-100%',
    position: 'fixed',
    top: pxToRem(81),
    transition: 'all .3s ease',
    visibility: 'hidden',
    width: '100%',
    zIndex: '1002',
    overflowY: 'auto',
    backgroundColor: theme.mixins.White(),
    padding: `0 ${theme.spacing(3)}`,
    '& .stickyPart': {
      position: 'sticky',
      paddingTop: theme.spacing(4),
      top: '0',
      backgroundColor: theme.mixins.White(),
      zIndex: '11',
      '& a': {
        display: 'block',
      },
    },

    '& .backButton': {
      marginBottom: theme.spacing(3),
      textAlign: theme.mixins.textLeft(),
      '& .MuiButton-textSecondary:hover': {
        backgroundColor: 'transparent',
        color: 'inherit',
      },
      '& .MuiButton-label': {
        color: theme.mixins.Black(),
      },
      '& .MuiButton-startIcon': {
        transform: 'rotate(180deg)',
        padding: '0',
        width: 'auto',
      },
      '& > span:hover': {
        '& .MuiButton-label': {
          color: 'inherit',
        },
        '& .MuiButton-startIcon': {
          color: 'inherit',
          backgroundColor: 'transparent',
        },
      },
    },
  },
  menuTitle: {
    marginBottom: theme.spacing(2.5),
    '& .MuiTypography-root': {
      ...theme.mixins.marginLeft(0),
      textAlign: theme.mixins.textLeft(),
    },
    '& .MuiAvatar-colorDefault': {
      transition: 'background-color 0.3s ease-in-out',
      backgroundColor: theme.mixins.AbuDhabiRed(),
    },
    '& .MuiAvatar-root .animateIcon': {
      height: theme.spacing(3),
    },
    [theme.breakpoints.up('lg')]: {
      marginBottom: theme.spacing(6.5),
      '&:hover': {
        '& .MuiTypography-root': {
          textDecoration: 'underline',
        },
        '& .MuiAvatar-colorDefault': {
          backgroundColor: theme.mixins.Black(),
        },
        '& .MuiAvatar-root .animateIcon': {
          animationName: '$arrowMove',
          animationDuration: '.5s',
          animationIterationCount: '1',
        },
      },
    },
  },
  mobileMegaMenu: {
    padding: 0,
    '& > li': {
      padding: 0,
      minHeight: 0,
      whiteSpace: 'normal',
      '& > a': {
        display: 'block',
        width: '100%',
        paddingTop: theme.spacing(1.5),
        paddingBottom: theme.spacing(1.5),
        textAlign: theme.mixins.textLeft(),
        '& .MuiSvgIcon-root': {
          fontSize: pxToRem(16),
          transform: theme.mixins.rotate0(),
          position: 'absolute',
          ...theme.mixins.right(0),
          top: `calc(50% - ${pxToRem(8)})`,
        },
      },
    },
  },
  desktopMegaMenuWrapper: {
    display: 'flex',
    width: '100%',
    transition: 'transform 0.3s',
    '&.megaMenuWrapper': {
      '&.hideL2List': {
        '& .l2List': {
          transform: `translateX(${theme.mixins.translateX(-100)}%)`,
          transition: 'transform 0.3s',
        },
      },
      '&:not(.hideL2List)': {
        '& .l3List': {
          transform: `translateX(${theme.mixins.translateX(100)}%)`,
          transition: 'transform 0.3s',
        },
      },
    },
  },
  desktopMegaMenu: {
    padding: `0 ${theme.spacing(4)} ${theme.spacing(4)} ${theme.spacing(9)}`,
    flex: '0 0 100%',
    maxWidth: '100%',
    '& .stickyPart': {
      position: 'sticky',
      paddingTop: theme.spacing(9),
      top: '0',
      backgroundColor: theme.mixins.White(),
      zIndex: '11',
      '& a': {
        display: 'block',
      },
    },
    '& .backButtonDesktop': {
      marginBottom: theme.spacing(4),

      '& .MuiButton-label': {
        color: theme.mixins.Black(),
      },
      '& .MuiButton-startIcon': {
        padding: '0',
        width: 'auto',
        transform: 'rotate(180deg)',
      },
      '& > span:hover': {
        '& .MuiButton-label': {
          color: theme.mixins.Black(),
        },
        '& .MuiButton-startIcon': {
          color: theme.mixins.Black(),
          backgroundColor: 'transparent',
        },
      },
    },
    '& .MuiTypography-h4': {
      ...theme.mixins.marginLeft(theme.spacing(-4)),
    },
    '& .MuiAvatar-root': {
      width: theme.spacing(6),
      height: theme.spacing(6),
      ...theme.mixins.marginLeft(theme.spacing(3)),
    },
    '& ul': {
      padding: 0,
      '& > li': {
        padding: 0,
        minHeight: 0,
        ...theme.mixins.borderLeft(theme.borders.divider),
        cursor: 'default',
        overflow: 'inherit',
        whiteSpace: 'normal',
        '&:hover': {
          backgroundColor: 'transparent',
        },
        '& > a': {
          display: 'inline-flex',
          alignItems: 'center',
          paddingTop: theme.spacing(1),
          paddingBottom: theme.spacing(1),
          position: 'relative',
          width: '100%',
          '&::after': {
            display: 'block',
            content: '""',
            backgroundColor: theme.mixins.AbuDhabiRed(),
            position: 'absolute',
            ...theme.mixins.left(0),
            top: 0,
            width: pxToRem(4),
            height: '100%',
            transform: 'scaleY(0)',
            transition: 'transform 0.3s ease-in-out',
          },
          '&:hover': {
            color: theme.mixins.AbuDhabiRed(),
            '&::after': {
              transform: 'scaleY(1)',
            },
            '& > span': {
              fontWeight: 'bold',
              pointerEvents: 'none',
            },
          },
          '&:focus': {
            outline: 'none',
            border: 0,
          },
          ...theme.mixins.paddingLeft(pxToRem(30)),
          '& .MuiSvgIcon-root': {
            ...theme.mixins.marginLeft(theme.spacing(1)),
            transform: theme.mixins.rotate0(),
            padding: theme.spacing(0.25),
            pointerEvents: 'none',
          },
        },
      },
    },
  },
  '@keyframes arrowMove': {
    '0%': { transform: 'translateX(0)' },
    '50%': { transform: `translateX(${theme.mixins.translateX(130)}%)` },
    '51%': { transform: `translateX(${theme.mixins.translateX(-130)}%)` },
    '100%': { transform: 'translateX(0)' },
  },
  '@global': {
    '.offScreen': {
      left: '-100%',
      opacity: '0',
    },
    '.onScreen': {
      left: '0',
      visibility: 'visible',
    },
    '.MuiAppBar-root.megaNavOpened .mega-navigation-list a:not(.active) span:not(:hover)': {
      [theme.breakpoints.up('lg')]: {
        color: theme.mixins.Black(),
      },
    },
    '.MuiAppBar-root.megaNavOpened': {
      zIndex: '1310',
      position: 'fixed',
      transform: 'translateY(0) !important',
      visibility: 'visible !important',
      color: theme.palette.secondary.contrastText,
      background: theme.palette.secondary.main,
      transition: 'none !important',
      '& .MuiGrid-root': {
        background: 'transparent',
      },
      '&.hidden': {
        transform: 'translateY(-14rem) !important',
      },
      '& .business-link': {
        borderColor: theme.mixins.Black(200),
      },
      '& .language-selector-wrapper': {
        '& svg path': {
          fill: theme.mixins.Black(),
        },
      },
      [theme.breakpoints.up('lg')]: {
        minHeight: theme.spacing(16),
        boxShadow: `0px 2px 4px 0px ${theme.mixins.Black(100)}`,
      },
    },
  },
}));

export default useStyles;
